}

/*! Reset classes */
.btn-reset {
  width: auto;
  margin: 0;
  padding: 0;
  background: transparent;
  outline: 0;
  color: inherit;
  font-family: inherit;
  font: inherit;
  cursor: pointer;
  -webkit-font-smoothing: inherit;
  -moz-osx-font-smoothing: inherit;
  -webkit-appearance: none;
}

.btn-reset::-moz-focus-inner {
  border: 0;
  padding: 0;
}

@media (min-width: 992px) {
  .btn-reset:focus {
    box-shadow: 0 0 0 3px #8edc44;
    border-radius: 8px;
  }
  .btn-reset:focus-visible {
    box-shadow: 0 0 0 3px #8edc44;
    border-radius: 8px;
  }
  .btn-reset:focus:not(:focus-visible) {
    box-shadow: none;
    border-radius: 8px;
  }
}

.link-reset {
  outline: 0;
  color: inherit;
  text-decoration: none;
}

.link-reset:visited {
  text-decoration: none;
}

@media (min-width: 992px) {
