.accordion {
  width: 100%;
}

.accordion__item {
  padding: 32px 0;
}

@media (max-width: 767.98px) {
  .accordion__item {
    padding: 24px 0;
  }
}

.accordion__item:first-child {
  padding-top: 0;
}

.accordion__item:last-child {
  padding-bottom: 0;
}

.accordion__item:not(:last-child) {
  border-bottom: 1px solid var(--color-mercury);
}

.accordion__trigger {
  position: relative;
  width: 100%;
  padding-right: 65px;
  text-align: left;
  font-weight: 600;
}

@media (max-width: 767.98px) {
  .accordion__trigger {
    padding-right: 32px;
  }
}

.accordion__trigger::before {
  content: "";
  position: absolute;
  top: 10px;
  right: 5px;
  width: 2px;
  height: 20px;
  background-color: var(--color-tamarillo);
  transform: translate(-9px, -50%);
  transition: transform 0.3s ease-out;
}

.accordion__trigger::after {
  content: "";
  position: absolute;
  top: 10px;
  right: 5px;
  width: 20px;
  height: 2px;
  background-color: var(--color-tamarillo);
  transform: translate(0%, -50%);
  transition: transform 0.3s ease-out;
}

.accordion__trigger--active::before {
  transform: translate(-9px, -50%) rotate(270deg);
  transition: transform 0.3s ease-out;
}

.accordion__trigger--active::after {
  transform: translate(0, -50%) rotate(180deg);
  transition: transform 0.3s ease-out;
}

.accordion__body {
  height: 0;
  overflow: hidden;
}

.accordion__body--show {
  height: auto;
}

.accordion__text {
  margin-top: 12px;
  margin-right: 55px;
}

@media (max-width: 767.98px) {
  .accordion__text {
    margin-top: 16px;
    margin-right: 0;
  }
}

